
import { defineComponent } from 'vue'
import { ICM212002SC00_REQ, ICM212002SC00_List, ICM212002SC00_RES } from '@/shared/type/icm212002sc00.type';
import { PAGE_SIZE } from '@/shared/constants/common.const';
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import Pagination from "@/shared/components/Pagination/Pagination.vue";
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";

export default defineComponent({
    name: 'icm212002sc00',
    components: {
        Pagination,
        BreadCrumb,
        SelectOption
    },
    data() {
        return {
            dataTable: [] as ICM212002SC00_List[],
            searchFields: {
                searchValue: "",
                statusCode: ''
            },
            totalItems: 0,
            currentPage: 1,
            pageSize: PAGE_SIZE,
            startingIndex: 1,
            resetPage: false,
        }
    },

    created() {
        this.requestProductCategoryList();
    },

    computed: {
        headers() {
            return [
                { columnName: "No." },
                { columnName: "View" },
                { columnName: "Product Category Code" },
                { columnName: "Product Category Name" },
                { columnName: "Status" },
                { columnName: "Description" },
            ]
        }
    },

    methods: {
        async requestProductCategoryList(mode?: string) {
            const body: ICM212002SC00_REQ = {
                pageNumber: mode == 'search' ? 0 : this.currentPage,
                pageSize: this.pageSize,
                searchValue: this.searchFields.searchValue,
                statusCode: this.searchFields.statusCode
            };

            if (mode === 'search') {
                this.currentPage = 1;
                this.resetPage = true;
            }

            const res = await this.$request.getRequest('ICM21200211', body, false) as ICM212002SC00_RES

            if (res.header.result) {
                this.dataTable = res.body.list;
                this.totalItems = res.body.totalCount;
            }
        },

        resetForm() {
            this.searchFields.searchValue = "";
            this.currentPage = 1;
            this.resetPage = true;
            this.searchFields.statusCode = ''

            this.requestProductCategoryList();
        },

        handleRegister() {
            this.$router.push({
                path: 'ICM212002SC01',
                query: {
                    type: 'register'
                }
            });
        },

        handleClickRow(index: number, rowData: ICM212002SC00_List) {
            this.$router.push({
                path: 'ICM212002SC01',
                query: {
                    type: 'edit',
                    categoryCode: rowData?.categoryCode,
                }
            });
        },

        handlePagination(pageNumber: number) {
            this.resetPage = false;
            this.currentPage = pageNumber;
            this.requestProductCategoryList();
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        },
    }
})
